body {
  margin: 0;
  font-family: Arial, sans-serif;
  color: white;
}

/* Layout Components */
.main-container {
  display: flex;
  flex-direction: column;
  height: calc(100vh - 45px);
}

/* Header Styles */
.header {
  background-color: #ff7043;
  padding: 10px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__logo {
  font-size: 20px;
  font-weight: bold;
}

/* Sidebar Styles */

.sidebar {
  width: 100%;
  background-color: #444;
  border-bottom: 1px solid #555;
  padding: 10px;
}

.sidebar__title {
  font-size: 20px;
  font-weight: bold;
  margin-top: 10px;
  margin-bottom: 20px;
}

.sidebar__menu {
  padding: 0;
  display: flex;
  justify-content: space-around;
}

.sidebar__item {
  margin-bottom: 10px;
}
.sidebar__item input[type="radio"] {
  display: none;
}
.sidebar__item label {
  display: inline-block;
  padding: 5px 30px;
  font-size: 16px;
  color: #ffffff;
  background-color: #616161;
  border: 1px solid #ffffff;
  border-radius: 4px;
  cursor: pointer;
  margin-right: 10px;
  transition: background-color 0.3s;
}
.sidebar__item label:hover {
  background-color: #757575;
}
.sidebar__item input[type="radio"]:checked + label {
  background-color: #757575;
}

/* Main Content Area */
.main-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  background-color: #333;
  color: white;
}

/* Toolbar Styles */
.toolbar {
  padding: 10px;
  border-bottom: 1px solid #555;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.toolbar__left,
.toolbar__right {
  display: flex;
  align-items: center;
}

/* Button Styles */
.button {
  margin: 0 5px;
  padding: 8px 15px;
  border: none;
  cursor: pointer;
  border-radius: 4px;
  font-size: 14px;
  color: white;
  background-color: #ff8c42;
  transition: background-color 0.3s, box-shadow 0.3s;
}

.button:hover {
  background-color: #e67e37;
}

.button:disabled {
  cursor: not-allowed;
  background-color: #555;
  opacity: 0.5;
}

/* Button Variants */
.button_small {
  margin: 0 3px;
  padding: 5px 10px;
}

.button_green {
  background-color: #4caf50;
}

.button_green:hover {
  background-color: #388e3c;
}

.button_red {
  background-color: #e74c3c;
}

.button_red:hover {
  background-color: #c0392b;
}

/* Editor Styles */
.editor {
  display: flex;
  flex: 1;
  flex-direction: column;
}

.editor__pane {
  flex: 1;
  padding: 10px;
  border-bottom: 1px solid #555;
}

.editor__pane:last-child {
  border-bottom: none;
}

.editor__pane pre {
  background-color: #444;
  padding: 10px;
  border: 1px solid #555;
  border-radius: 4px;
  overflow: auto;
}

.editor__output-button-container {
  display: flex;
  justify-content: flex-end;
}

/* Input List Styles */
.input-list {
  list-style-type: none;
  padding-left: 20px;
}

.input-item {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-top: 10px;
  padding-left: 20px;
  min-width: fit-content;
  position: relative;
}

.input-item::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 2px;
  background-color: #ff8c42;
}

.input-item__content {
  display: flex;
  justify-content: center;
  align-items: center;
}

.input-item__controls {
  display: flex;
  justify-content: center;
  align-items: center;
}

.input__buttons-container {
  min-width: fit-content;
}

.input-item__field {
  margin-right: 10px;
  padding: 5px;
  border: 1px solid #555;
  border-radius: 4px;
  background-color: #444;
  color: white;
  width: 100px;
}

.input-item__field:disabled {
  background-color: #333;
}
.input-item__value_type {
  background-color: #555;
  border: 1px solid #444;
  color: #fff;
  padding: 5px;
  margin-right: 10px;
  width: 100px;
}
.type-label {
  color: #ffa500;
  margin-right: 10px;
}

/* Modal Styles */
.modal {
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.4);
}

.modal__content {
  background-color: #333;
  margin: 15% auto;
  padding: 20px;
  border: 1px solid #888;
  width: 80%;
  max-width: 400px;
  border-radius: 4px;
}

.modal__buttons {
  display: flex;
  justify-content: flex-end;
}

/* Notification Styles */
.notification {
  position: fixed;
  bottom: 20px;
  right: 20px;
  background-color: #4caf50;
  color: white;
  padding: 0 50px;
  border-radius: 4px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}

/* Icon Styles */
.icon {
  pointer-events: none;
}

/* Media Queries */
@media (min-width: 768px) {
  .main-container {
    flex-direction: row;
  }

  .sidebar {
    width: 110px;
    border-bottom: none;
    border-right: 1px solid #555;
  }

  .sidebar__menu {
    flex-direction: column;
    justify-content: flex-start;
  }

  .editor {
    flex-direction: row;
  }

  .editor__pane {
    border-bottom: none;
    border-right: 1px solid #555;
  }

  .editor__pane:last-child {
    border-right: none;
  }
}
